
.map{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid red;
}

.map2{
    display: flex;
    flex-direction: column;
}


.map img{
    width: 80px;
    height: 80px;
    object-fit: cover;
}
.map button{
    width: 40px;
    height: 40px;
}
.map input{
    width: 73px;
    height: 35px;
}
.buttons{
    display: flex;
    flex-direction: column;
}